.project-card {
  transition: 0.5s;
  font-weight: 500;
  margin: 20px;
  padding: 8px;
  border: 1px solid;
  cursor: pointer;
}

.keywords {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
  justify-content: space-between;
  align-items: center;
}

.Architecture {
  border: 1px solid black;
  box-shadow: 10px 10px 10px 0 rgba(255, 234, 0, 0.3);
}

.Urbanism {
  border: 1px solid black;
  box-shadow: 10px 10px 10px 0 rgba(111, 255, 0, 0.3);
}

.Design {
  border: 1px solid black;
  box-shadow: 10px 10px 10px 0 rgba(255, 0, 0, 0.3);
}

.Art {
  border: 1px solid black;
  box-shadow: 10px 10px 10px 0 rgba(8, 0, 255, 0.3);
}

.Architecture:hover {
  border: 1px solid rgba(255, 234, 0);
  box-shadow: 10px 10px 0 0 rgba(255, 234, 0, 1);
}

.Urbanism:hover {
  border: 1px solid rgba(111, 255, 0);
  box-shadow: 10px 10px 0 0 rgba(111, 255, 0, 1);
}

.Design:hover {
  border: 1px solid rgba(255, 0, 0);
  box-shadow: 10px 10px 0 0 rgba(255, 0, 0, 1);
}

.Art:hover {
  border: 1px solid rgba(8, 0, 255);
  box-shadow: 10px 10px 0 0 rgba(8, 0, 255, 1);
}
