.text-block {
  margin: auto;
  max-width: 1200px;
  padding: 0 20px;
  border-top: 2px solid;
  border-bottom: 2px solid;
}

.text-block h1 {
  font-size: 2.5rem;
  font-weight: 700;
  margin: 0;
  padding: 20px 0;
}

.text-block p {
  font-size: 1.5rem;
  font-weight: 400;
  margin: 0;
  padding: 20px 0;
}

.text-block.image {
  max-height: 100svh;
  display: flex;
  flex-direction: row;
}

.text-block.image img {
  max-width: 30%;
  max-height: 100svh;
  object-fit: fill;
  overflow: hidden;
}

.text-block.image.left {
  justify-content: flex-start;
}

.text-block.image.right {
  justify-content: flex-end;
}
